.navbar{
	position:fixed;
	height: 80px;
	/* background-color: hsl(217,32%,17%); */
	text-align:center;
  width: auto;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background: linear-gradient(180deg, rgba(51,65,85,1) 0%, rgba(15,23,42,1) 100%);
  width: 14rem;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  /* position: fixed; */
  top: 0;
  left: -100%;
  transition: 850ms;
  font-weight: 600;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  /* width: 95%; */
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}