.characters {
  background-color: hsl(217,32%,17%);
  /* height: 80px; */
  display: flex;
  justify-content: start;
  align-items: center;
  margin: 20px;
}

.characters-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.characters-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.button-add {
  min-width: 100px;
  padding: 16px 32px;
  border-radius: 4px;
  border: none;
  background: hsl(198,88%,48%);
  color: #fff;
  font-size: 24px;
  cursor: pointer;
}

div .container {
  display: flex;
  flex-direction: column;
  /* justify-items: start; */
}

div .image {
  background-image:var(--characterImage);
}
